@use "/src/utilities/variables";

.projects {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.box {
  //height: 80%;
  // width: 50%;
  margin: 10px;
  display: flex;
  flex-direction: column;
  //justify-content: center;
  //align-items: center;

  border: 1px solid transparent;
  color: map-get(variables.$colors, "default_white");
  border-color: map-get(variables.$colors, "default_white");

  img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }

  Button {
    border: none;
    border-left: 1px solid transparent;
    border-color: map-get(variables.$colors, "default_white");
  }

  overflow: hidden;
}

.title {
  padding: 1em;

  border-bottom: 1px solid transparent;
  border-color: map-get(variables.$colors, "default_white");

  font-size: 1rem;
}

.download {
  display: flex;
  flex-direction: row;

  .text {
    width: 100%;
    padding: 1em;
    line-height: 1;
    margin: auto;
  }

  border-top: 1px solid transparent;
  border-color: map-get(variables.$colors, "default_white");
}

@media only screen and (max-height: 700px) {
  .title {
    font-size: 1rem;
  }
  .text {
    font-size: 1rem;
  }
}

@media only screen and ((max-height: 500px) or (max-width: 850px)) {
  .title {
    font-size: 0.5rem;
  }
  .text {
    font-size: 0.5rem;
  }
  button {
    font-size: 0.5rem !important ;
  }
}
