@use "/src/utilities/variables";

.button {
  border: 1px solid transparent;

  padding: 1em 1.5em;

  font-family: "Pixelify";
  font-size: 1rem;
  font-weight: bold;

  text-transform: uppercase;

  color: map-get(variables.$colors, "default_white");
  border-color: map-get(variables.$colors, "default_white");
  background-color: transparent;

  transition: color 100ms ease-in-out, background-color 100ms ease-in-out,
    border-color 100ms ease-in-out, box-shadow 100ms ease-in-out;
  cursor: pointer;
}
.button:hover {
  color: map-get(variables.$colors, "bg_color");
  border-color: map-get(variables.$colors, "default_white");
  background-color: map-get(variables.$colors, "default_white");
}

@media only screen and (max-height: 500px) {
  .button {
    font-size: 5vh;
  }
}
