@use "/src/utilities/variables";

.header {
  height: 60px;
  display: block;
  position: sticky;
  top: 0;
  background-color: map-get(variables.$colors, "bg_color");
  z-index: 10;
}

.text {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  text-align: left;
  cursor: pointer;
  div {
    width: 100%;
  }
}

.menu {
  width: 100%;
  display: flex;
  justify-content: right;
  align-items: end;
  div {
    margin-bottom: 3px;
  }
}

.bar1,
.bar2,
.bar3 {
  width: 20px;
  height: 3px;
  background-color: map-get(variables.$colors, "default_white");
  margin: 3px 0;
  transition: 0.4s;
}

.change .bar1 {
  transform: translate(0, 6px) rotate(-45deg);
  background-color: map-get(variables.$colors, "default_white");
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  transform: translate(0, -6px) rotate(45deg);
  background-color: map-get(variables.$colors, "default_white");
}

.dropdown {
  height: 0;
  overflow: hidden;
  .open {
    height: auto;
  }
}

.dropdownItem {
  height: 60px;
  border-bottom: 1px solid map-get(variables.$colors, "default_white");

  display: flex;
  flex-direction: row;

  font-size: 1rem;
  background-color: map-get(variables.$colors, "bg_color");
}
