@use "utilities/variables";

@font-face {
  font-family: "Pixelify";
  src: url(./fonts/PixelifySans-Regular.ttf);
  font-weight: 200;
}

@font-face {
  font-family: "Pixelify";
  src: url(./fonts/PixelifySans-SemiBold.ttf);
  font-weight: 600;
}

html {
  height: 100%;
  width: 100%;

  color: map-get(variables.$colors, "default_white");
  background-color: map-get(variables.$colors, "bg_color");
  font-family: "Pixelify";
  text-decoration: none;
  font-size: 1.5rem;
}

body {
  width: 96%;
  height: 100%;
  margin: auto;
}

a {
  color: map-get(variables.$colors, "default_white");
  text-decoration: none;
}

.page {
  height: calc(100vh - 122px);
  display: flex;
  flex-direction: column;
  background-color: map-get(variables.$colors, "def_white");
}
