@use "/src/utilities/variables";

.welcome {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  div {
    margin: 10px;
  }

  img {
    max-width: 80%;
    max-height: 60%;
  }
}

.text {
  font-size: 2rem;
}

@media only screen and (max-height: 700px) {
  .text {
    font-size: 1rem;
  }
}

@media only screen and (max-height: 500px) {
  .text {
    font-size: 5vh;
  }
}
